import React,{useState,useEffect} from "react"
import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Image from 'react-bootstrap/Image'
import axios from "axios"
import './body.css'
import $ from 'jquery'
import Button from "react-bootstrap/Button"
import Product from "./product-detail"
import {BrowserRouter as Router, Route,Link,Switch} from 'react-router-dom';
function Body(){
	const [products,setProducts]=useState([])
  const [productid,setProductid]=useState(0)
  const handleProductSelect=(event)=>{
      $(".margin-container").css("display","none");
      $("#logo").css("display","none");
      setProductid(event.target.id)
}
	useEffect(()=>{
        axios.get("products.json")
        .then((response)=>{
        	setProducts(response.data)
        	console.log(response)
        })
        .catch((error)=>{
        	console.log(error)
        })
	},[])
	return(  <div>
           <Router>
           <div className="margin-container">
           <span className="mt-0 ml-5 title text-center body-container">Products</span>
           <Container>
           {
           	products.map((product)=>(
            <Link to="/product">
           		       <div key={product.id} >
                     <Image src={product.imgthumb}
                     onClick={handleProductSelect} 
                     className="mb-5 ml-5" id={product.id} rounded fluid />
           		       </div>
                     </Link>
           		))
           }
           </Container>
           </div>
           <Switch>
           <Route path="/product">
          <Product id={productid}/>
           </Route>
           </Switch>
           </Router>
           </div>
		) 
}
export default Body